input::-webkit-calendar-picker-indicator{
    display: none;
}

.register_comp_wrapper{
    width: 100%;
    min-height: 100vh;
    background-color: whitesmoke;
    font-family: 'HelveticaNeue', Arial, sans-serif;
}

.back_click{
    width: 45px;
    height: 45px;
    position: absolute;
    top: 20px;
    left: 45px;
}


.register_comp{
    margin: auto;
    width: 55%;
    min-height: 100vh;
    display: grid;
    grid-template-rows: auto 1fr auto;
    align-content: space-between;
}

.header_comp{
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    align-items: center;
    height: 11vh;
    grid-column-gap: 10%;
}
.comp_header_phone_1{
    display: none;
}
.active_comp{
    justify-self: start;
    font-size: 17px;
    position: relative;
}
.active_comp::before {
    content: '';
    display: block;
    position: absolute;
    bottom: -20px;
    width: 80%;
    left: 10%;
    border-top: 2px solid black

}
.inctive_comp{
    justify-self: end;
    font-size: 17px;
}

.register_comp_main{
    background-color: #ffffff;
    width: 100%;
    border-radius: 18px;
    padding: 5% 15% 4.5% 15%;
}

.register_comp_form {
    height: 100%;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(9, auto);
    align-content: space-between;
    align-items: center;
    row-gap: 45px;
    grid-template-areas: 
            "file file file file file file"
            "compName compName compName brands brands brands"
            "hr hr hr hr hr hr"
            "cpus cpus cpuCores cpuCores cpuThread cpuThread "
            "ram ram ram memoType memoType memoType"
            "hr1 hr1 hr1 hr1 hr1 hr1"
            "date date date price price price"
            "conditions conditions conditions . . ."
            "back back . . save save"
}

option:checked { 
    background-color: #E7F0F8; 
}


.register_comp_form .file, .register_comp_form .file_val {
    grid-area: file
}
.register_comp_form .compName {
    grid-area: compName
}

.register_comp_form .brands {
    grid-area: brands
}

.register_comp_form .hr {
    grid-area: hr
}

.register_comp_form .cpus {
    grid-area: cpus
}

.register_comp_form .cpuCores {
    grid-area: cpuCores
}

.register_comp_form .cpuThread {
    grid-area: cpuThread
}

.register_comp_form .ram {
    grid-area: ram
}
.register_comp_form .radiowraper_memoType {
    grid-area: memoType
}

.register_comp_form .hr1 {
    grid-area: hr1
}

.register_comp_form .date {
    grid-area: date
}
.register_comp_form .price {
    grid-area: price
}
.register_comp_form .radiowraper_conditions {
    grid-area: conditions
}

.register_comp_form button{
    grid-area: save
}

.register_comp_form .back{
    grid-area: back
}


.file, .compName, .brands, .cpus, .cpuCores, .cpuThread, .ram, .date, .price {
    width: 100%;
    display: grid;
    grid-template-rows: auto 1fr auto;
    padding: 0px 14px 0 14px;
}

.memoType, .conditions {
    display: grid;
    grid-template-columns: 5% 10% 5% 10%;
    align-items: center;
    justify-items: start;
    column-gap: 15px;
    font-size: 14px;
    
}
.memoType input, .conditions input {
    height: 15px;
}

.radiowraper_memoType, .radiowraper_conditions {
    height: 100%;
    display: grid;
    grid-template-rows: auto 1fr auto;
    align-items: center;
    padding-left: 14px;

}

.radiowraper_memoType p, .radiowraper_conditions p{
    font-size: 15px;
    margin-bottom: 5px;
    font-weight: bold;
   
}

.label_radio{
    line-height: 44px;
}


.hr, .hr1{
    color:  #C7C7C7;
}

input[type="date"]{
    line-height: 40px;
    border-radius: 8px;
    border: 1.8px solid #8AC0E2;
    padding-left: 10px;
    font-size: 13px;
}

input[type='radio'] {
    -webkit-appearance:none;
    width:20px;
    height:20px;
    border:1.8px solid #8AC0E2;
    border-radius:50%;
    outline:none;
    
}

input[type='radio']:before {
    content:'';
    display:block;
    width:60%;
    height:60%;
    margin: 20% auto;    
    border-radius:50%;    
}

input[type='radio']:checked:before {
    background: #8AC0E2;    
}

.file {
    position: relative;
    background-color: #F7F7F7;
    border: 2px dashed #4386A9;
    border-radius: 18px;
    min-height:300px; 
    padding-top: 30px; 
    padding-bottom: 30px; 
    display: grid;
    grid-template-rows: 70px 70px;
    grid-template-columns: 200px;
    align-content: center;
    justify-content: center;
    justify-items: center;
    color: #4386A9
}

.file_val{
    position: relative;
    background-color: #FFF1F1;
    border: 2px dashed red;
    border-radius: 18px;
    min-height:300px; 
    display: grid;
    grid-template-rows: 70px 70px;
    grid-template-columns: 200px;
    row-gap: 30px;
    align-content: center;
    justify-content: center;
    justify-items: center;
    color: #4386A9
    
}
.file_val p{
    background-image: url(../../assets/Vector.png);
    background-repeat: no-repeat;
    background-position: top;
    background-size: 25px;
    padding-top: 25px;
    margin-bottom: 25px;
    height: 100px;
}


.upload_info {
    text-align: center;
}
.upload_info_phone{
    display: none;
}
.camera_photo{
    display: none;
}


input[type='file'] {
    opacity: 0;
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.file_button {
    height: 40px;
    width: 200px;
    background-color: #62A1EB;
    color: #ffffff;
    border-radius: 18px;
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: center;
}
.file_button:hover{
    background-color: #317AD0;
}

.file_button:focus{
    background-color: #1A5DAB;
}
.file img{
    max-width: 100px;
    max-height: 100px;
}

.back{
    text-decoration: none;
    color: #62A1EB;
    font-size: 14px;
}
input:focus{
    background-color: rgba(43, 75, 242, 0.06);
    border: 1.8px solid #6499FF;
}
input:invalid[focused="true"][name="compName"] ~ p{
    color: red
}

input:invalid[focused="true"][name="compName"] {
    border: 1.8px solid red
}

input:invalid[focused="true"][name="cpuCores"] ~ p{
    color: red
}

input:invalid[focused="true"][name="cpuCores"] {
    border: 1.8px solid red
}

input:invalid[focused="true"][name="cpuThread"] ~ p{
    color: red
}

input:invalid[focused="true"][name="cpuThread"] {
    border: 1.8px solid red
}

input:invalid[focused="true"][name="ram"] ~ p{
    color: red
}

input:invalid[focused="true"][name="ram"] {
    border: 1.8px solid red
}

input:invalid[focused="true"][name="price"] ~ p{
    color: red
}

input:invalid[focused="true"][name="price"] {
    border: 1.8px solid red
}

.memoType_radio_val{
    color: red;
    background-image: url(../../assets/Vector.png);
    background-repeat: no-repeat;
    background-position: left;
    background-size: 15px;
    padding-left: 20px;
} 

.conditions_radio_val{
    color: red;
    background-image: url(../../assets/Vector.png);
    background-repeat: no-repeat;
    background-position: left;
    background-size: 15px;
    padding-left: 20px;
}

@media only screen and (max-width: 390px){
    .header_comp{
        display: none;
    }
    .comp_header_phone_1{
        display: block;
        display: grid;
        justify-items: center;
        align-items: end;
        height: 80px;
    }
    
    .register_comp_wrapper .back_click_phone{
        display: block;
        width: 10px;
        height: 15px;
        position: absolute;
        top: 35px;
        left: 20px;
    }

    .footer_logo{
        display: none;
    }
    .register_comp{
        min-height: 100vh;
        width: 100%;
    
    }
    .register_comp_main{
        width: 100%;
        border-radius: 0px;
        padding: 0 0 0 0;
    }

    .register_comp_form {
        height: 100%;
        display: grid;
        width: 100%;
        grid-template-columns: repeat(6, 1fr);
        grid-template-rows: repeat(14, auto);
        align-content: space-between;
        align-items: center;
        padding-bottom: 30px;
        grid-template-areas: 
                "file file file file file file"
                "compName compName compName compName compName compName "
                "brands brands brands brands brands brands"
                "hr hr hr hr hr hr"
                "cpus cpus cpus  cpus cpus cpus "
                "cpuCores cpuCores cpuCores cpuCores  cpuCores cpuCores" 
                "cpuThread cpuThread cpuThread cpuThread cpuThread cpuThread"
                "ram ram ram ram ram ram"
                "memoType memoType memoType memoType memoType memoType"
                "hr1 hr1 hr1 hr1 hr1 hr1"
                "date date date date date date"
                "price price price price price price"
                "conditions conditions conditions conditions conditions conditions"
                "back back . . save save"
    }
    .hr, .hr1{
        width: 100%;
        height: 10px;
        background-color:whitesmoke;
        color:  #C7C7C7;
        border: none
    }

    .back{
        padding-left: 20px;
    }

    .file{
        justify-self: center;
        width: 90%;
    }

    .upload_info {
        display: none;
    }

    .file_button{
        display: none;
    }


    .upload_info_phone{
        text-align: center;
        display: block;
    }
    .camera_photo{
        display: block;
    }
}